import {
  Text,
  Dropdown,
  Pivot,
  PivotItem,
  PivotLinkFormat,
  DetailsList,
  DetailsListLayoutMode,
  SelectionMode,
  TooltipHost,
  IColumn,
  IDropdownOption,
  ActionButton,
} from 'office-ui-fabric-react/lib/'
import { DefaultButton, IconButton, PrimaryButton } from 'office-ui-fabric-react/lib/Button'
import { Stack } from 'office-ui-fabric-react/lib/Stack'
import React from 'react'

const optionsOne: IDropdownOption[] = [{ key: 'apple', text: 'Српски језик' }]
const optionsTwo: IDropdownOption[] = [{ key: 'apple', text: 'Основна школа' }]

const items: any = [
  {
    key: 0,
    name: 'Српски језик',
    tipPredmeta: 'Обавезни предмет',
    varijanta: 'А',
    komplet: 'Комплет',
  },
  {
    key: 1,
    name: 'Српски језик',
    tipPredmeta: 'Обавезни предмет',
    varijanta: 'Б',
    komplet: 'Комплет',
  },
  {
    key: 2,
    name: 'Математика',
    tipPredmeta: 'Обавезни предмет',
    varijanta: '',
    komplet: '',
  },
  {
    key: 3,
    name: 'Грађанско васпитање',
    tipPredmeta: 'Изборни предмет',
    varijanta: '',
    komplet: '',
  },
]

const columns: IColumn[] = [
  {
    key: 'column1',
    name: 'Предмет',
    fieldName: 'name',
    minWidth: 16,
    maxWidth: 150,
  },
  {
    key: 'column2',
    name: 'Тип предмета',
    fieldName: 'tipPredmeta',
    minWidth: 16,
    maxWidth: 150,
  },
  {
    key: 'column3',
    name: 'Варијанта',
    fieldName: 'varijanta',
    minWidth: 16,
    maxWidth: 100,
  },
  {
    key: 'column4',
    name: 'Комплет',
    fieldName: 'komplet',
    onRender: () => (
      <ActionButton
        styles={{ root: { height: 'auto', marginLeft: -8, color: '#0c4076' } }}
        iconProps={{ iconName: 'Documentation' }}
        text="Комплет"
      />
    ),
    minWidth: 16,
    maxWidth: 100,
  },
]

export const OsnovnaSkolaJedan = () => {
  return (
    <>
      <Text styles={{ root: { fontSize: 18, fontWeight: 600 } }}>План уџбеника</Text>
      <Stack horizontal={true} tokens={{ childrenGap: 10 }} styles={{ root: { marginTop: 30 } }}>
        <Dropdown label="Језик наставе" options={optionsOne} selectedKey="apple" />
        <Dropdown label="Врста установе" options={optionsTwo} selectedKey="apple" />
        <PrimaryButton
          styles={{ root: { alignSelf: 'end', backgroundColor: '#218135', border: '1px solid #218135' } }}
          text="Прикажи"
        />
      </Stack>
      <Stack styles={{ root: { marginTop: 30 } }}>
        <Text styles={{ root: { fontSize: 18, fontWeight: 600, marginBottom: 20 } }}>Разреди</Text>
        <Pivot
          linkFormat={PivotLinkFormat.tabs}
          styles={{
            link: {
              width: 50,
              backgroundColor: '#f8f8f8',
              ':hover': { backgroundColor: '#ddd', color: '#000' },
              '&.is-selected': { backgroundColor: '#10549a' },
            },
            linkIsSelected: {
              width: 50,
            },
            itemContainer: {
              marginTop: 30,
            },
          }}
        >
          <PivotItem headerText="I">
            <Stack horizontal={true} horizontalAlign="end">
              <TooltipHost content="Додај комплет">
                <IconButton iconProps={{ iconName: 'PageAdd' }} />
              </TooltipHost>
              <TooltipHost content="Измени комплет">
                <IconButton iconProps={{ iconName: 'PageEdit' }} />
              </TooltipHost>
              <TooltipHost content="Избриши комплет">
                <IconButton iconProps={{ iconName: 'PageRemove' }} />
              </TooltipHost>
            </Stack>
            <DetailsList
              items={items}
              columns={columns}
              selectionMode={SelectionMode.none}
              layoutMode={DetailsListLayoutMode.justified}
              isHeaderVisible={true}
              styles={{ root: { border: '1px solid #ddd', overflow: 'hidden' } }}
            />
          </PivotItem>
          <PivotItem headerText="II">2</PivotItem>
          <PivotItem headerText="III">3</PivotItem>
          <PivotItem headerText="IV">4</PivotItem>
          <PivotItem headerText="V">5</PivotItem>
          <PivotItem headerText="VI">6</PivotItem>
          <PivotItem headerText="VII">7</PivotItem>
          <PivotItem headerText="VIII">8</PivotItem>
        </Pivot>
      </Stack>
    </>
  )
}
