import {
  Text,
  Dropdown,
  Pivot,
  PivotItem,
  PivotLinkFormat,
  DetailsList,
  DetailsListLayoutMode,
  SelectionMode,
  TooltipHost,
  IColumn,
  IDropdownOption,
  TextField,
  ActionButton,
} from 'office-ui-fabric-react/lib/'
import { IconButton, PrimaryButton } from 'office-ui-fabric-react/lib/Button'
import { Stack } from 'office-ui-fabric-react/lib/Stack'
import React from 'react'

const optionsOne: IDropdownOption[] = [{ key: 'apple', text: 'Српски језик' }]
const optionsTwo: IDropdownOption[] = [{ key: 'apple', text: 'Средња гимназија школа' }]
const optionsThree: IDropdownOption[] = [
  { key: 'apple', text: 'Природно-математички' },
  { key: '1', text: 'Општи' },
  { key: '2', text: 'Ученици са посебним способностима за филолошке науке - живи језици' },
  { key: '3', text: 'Ученици са посебним способностима за филолошке науке - класични језици' },
]

const items: any = [
  {
    key: 0,
    name: 'Математика',
    tipPredmeta: 'Обавезни предмет',
    varijanta: '',
    komplet: 'Комплет',
  },
  {
    key: 1,
    name: 'Географија',
    tipPredmeta: 'Обавезни предмет',
    varijanta: '',
    komplet: 'Комплет',
  },
  {
    key: 2,
    name: 'Уметност и дизајн',
    tipPredmeta: 'Изборни предмет',
    varijanta: '',
    komplet: '',
  },
]

const columns: IColumn[] = [
  {
    key: 'column1',
    name: 'Предмет',
    fieldName: 'name',
    minWidth: 16,
    maxWidth: 150,
  },
  {
    key: 'column2',
    name: 'Тип предмета',
    fieldName: 'tipPredmeta',
    minWidth: 16,
    maxWidth: 150,
  },
  {
    key: 'column3',
    name: 'Варијанта',
    fieldName: 'varijanta',
    minWidth: 16,
    maxWidth: 100,
  },
  {
    key: 'column4',
    name: 'Комплет',
    fieldName: 'komplet',
    minWidth: 16,
    maxWidth: 100,
    onRender: (item) => {
      if (item && item.key === 2) return ''
      return (
        <ActionButton
          styles={{ root: { height: 'auto', marginLeft: -8, color: '#0c4076' } }}
          iconProps={{ iconName: 'Documentation' }}
          text="Комплет"
        />
      )
    },
  },
]

export const GimnazijaJedan = () => {
  return (
    <>
      <Text styles={{ root: { fontSize: 18, fontWeight: 600 } }}>План уџбеника</Text>
      <Stack grow={1} tokens={{ childrenGap: 10 }} styles={{ root: { marginTop: 30, width: 735 } }}>
        <Stack horizontal={true} tokens={{ childrenGap: 10 }}>
          <Dropdown label="Језик наставе" options={optionsOne} selectedKey="apple" />
          <Dropdown label="Врста установе" options={optionsTwo} selectedKey="apple" />
        </Stack>
        <Stack grow={1} horizontal={true} tokens={{ childrenGap: 10 }}>
          <TextField label="Подручје рада" />
          <TextField label="Образовни профил" />
          <Dropdown label="Смер" dropdownWidth={550} options={optionsThree} selectedKey="apple" />
          <TextField label="Одсек" />
        </Stack>
        <PrimaryButton
          styles={{ root: { alignSelf: 'end', backgroundColor: '#218135', border: '1px solid #218135' } }}
          text="Прикажи"
        />
      </Stack>
      <Stack styles={{ root: { marginTop: 30 } }}>
        <Text styles={{ root: { fontSize: 18, fontWeight: 600, marginBottom: 20 } }}>Разреди</Text>
        <Pivot
          linkFormat={PivotLinkFormat.tabs}
          styles={{
            link: {
              width: 50,
              backgroundColor: '#f8f8f8',
              ':hover': { backgroundColor: '#ddd', color: '#000' },
              '&.is-selected': { backgroundColor: '#10549a' },
            },
            linkIsSelected: {
              width: 50,
            },
            itemContainer: {
              marginTop: 30,
            },
          }}
        >
          <PivotItem headerText="I">
            <Stack horizontal={true} horizontalAlign="end">
              <TooltipHost content="Додај комплет">
                <IconButton iconProps={{ iconName: 'PageAdd' }} />
              </TooltipHost>
              <TooltipHost content="Измени комплет">
                <IconButton iconProps={{ iconName: 'PageEdit' }} />
              </TooltipHost>
              <TooltipHost content="Избриши комплет">
                <IconButton iconProps={{ iconName: 'PageRemove' }} />
              </TooltipHost>
            </Stack>
            <DetailsList
              items={items}
              columns={columns}
              selectionMode={SelectionMode.none}
              layoutMode={DetailsListLayoutMode.justified}
              isHeaderVisible={true}
              styles={{ root: { border: '1px solid #ddd', overflow: 'hidden' } }}
            />
          </PivotItem>
          <PivotItem headerText="II">2</PivotItem>
          <PivotItem headerText="III">3</PivotItem>
          <PivotItem headerText="IV">4</PivotItem>
        </Pivot>
      </Stack>
    </>
  )
}
