import { Theme, createTheme } from '@fluentui/theme'
import { mergeStyles } from 'office-ui-fabric-react'

mergeStyles({
  selectors: {
    ':global(*)': {
      margin: 0,
      padding: 0,
    },
    ':global(html), :global(body), :global(#app)': {
      height: '100%',
    },
    ':global(body)': {
      fontFamily: 'Arial, Helvetica, sans-serif',
    },
    // TODO Ovo kroz TextField
    ':global(.is-disabled input, .is-disabled label)': {
      color: '#444',
    },
  },
})

export const AppTheme: Theme = createTheme({
  defaultFontStyle: {
    fontFamily: 'Arial, Helvetica, sans-serif',
  },
  components: {
    DetailsHeader: {
      styles: {
        root: {
          selectors: {
            '.ms-DetailsHeader-cellName': {
              fontWeight: 'bold',
              fontSize: 13,
            },
          },
          backgroundColor: '#f8f8f8',
          borderBottom: '1px solid #dddddd',
          paddingTop: 1,
        },
      },
    },
    DetailsList: {
      styles: {
        focusZone: {
          selectors: {
            '.dropdown-inside-column': {
              visibility: 'hidden',
            },
            '.ms-DetailsRow:hover .dropdown-inside-column': {
              visibility: 'visible',
              backgroundColor: '#eaeaea',
            },
            '.ms-DetailsRow:hover .dropdown-inside-column:hover': {
              backgroundColor: '#dddddd',
            },
            '.is-selected .dropdown-inside-column': {
              visibility: 'visible',
              backgroundColor: 'transparent',
            },
            '.dropdown-inside-column-wrapper': {
              display: 'flex',
              justifyContent: 'space-between',
            },
            '.dropdown-inside-column-wrapper > span': {
              overflow: 'hidden',
              whiteSpace: 'nowrap',
              textOverflow: 'ellipsis',
            },
          },
        },
      },
    },
    TextField: {
      styles: {
        root: {
          selectors: {
            '.ms-Label': {
              fontWeight: 'normal',
            },
          },
        },
      },
    },
    Dropdown: {
      styles: {
        label: {
          fontWeight: 'normal',
        },
      },
    },
  },
  palette: {
    themePrimary: '#0c4076',
    themeLighterAlt: '#f1f5f9',
    themeLighter: '#c7d8e9',
    themeLight: '#9cb8d6',
    themeTertiary: '#4f7cac',
    themeSecondary: '#1c4f86',
    themeDarkAlt: '#0b386a',
    themeDark: '#093059',
    themeDarker: '#072342',
    neutralLighterAlt: '#f8f8f8',
    neutralLighter: '#f4f4f4',
    neutralLight: '#eaeaea',
    neutralQuaternaryAlt: '#dadada',
    neutralQuaternary: '#d0d0d0',
    neutralTertiaryAlt: '#c8c8c8',
    neutralTertiary: '#595959',
    neutralSecondary: '#373737',
    neutralPrimaryAlt: '#2f2f2f',
    neutralPrimary: '#000000',
    neutralDark: '#151515',
    black: '#0b0b0b',
    white: '#ffffff',
  },
  semanticColors: {
    inputBorder: '#c2c2c2',
    inputBorderHovered: '#c2c2c2',
  },
})
