import { useBoolean } from '@uifabric/react-hooks'
import {
  Icon,
  Text,
  Dropdown,
  IDropdownOption,
  Pivot,
  PivotItem,
  PivotLinkFormat,
  DetailsList,
  DetailsListLayoutMode,
  Selection,
  SelectionMode,
  IColumn,
  DefaultButton,
  TooltipHost,
} from 'office-ui-fabric-react/lib/'
import { Breadcrumb, IBreadcrumbItem } from 'office-ui-fabric-react/lib/Breadcrumb'
import { IconButton, PrimaryButton } from 'office-ui-fabric-react/lib/Button'
import { Nav, INavLinkGroup } from 'office-ui-fabric-react/lib/Nav'
import { Panel, PanelType } from 'office-ui-fabric-react/lib/Panel'
import { IPersonaSharedProps, Persona, PersonaSize, PersonaPresence } from 'office-ui-fabric-react/lib/Persona'
import { Separator } from 'office-ui-fabric-react/lib/Separator'
import { Stack } from 'office-ui-fabric-react/lib/Stack'
import { TextField } from 'office-ui-fabric-react/lib/TextField'
import React, { useEffect } from 'react'
import { GimnazijaDva } from '../Components/Gimnazija/gimnazijadva'
import { GimnazijaJedan } from '../Components/Gimnazija/gimnazijajedan'
import { OsnovnaSkolaDva } from '../Components/OsnovnaSkola/osnovnaskoladva'
import { OsnovnaSkolaJedan } from '../Components/OsnovnaSkola/osnovnaskolajedan'
import { SSDva } from '../Components/SS/ssdva'
import { SSJedan } from '../Components/SS/ssjedan'
import { UmetnickaDva } from '../Components/Umetnicka/umetnickadva'
import { UmetnickaJedan } from '../Components/Umetnicka/umetnickajedan'

const examplePersona: IPersonaSharedProps = {
  imageUrl:
    'https://media-exp1.licdn.com/dms/image/C5603AQGMqGUznb0EMA/profile-displayphoto-shrink_200_200/0?e=1609372800&v=beta&t=_IqV-s7gXob4UP1rPqy6DZucR7a9VSHntMnfoWU_1pY',
  imageInitials: 'MS',
  text: 'Marko Savic',
  secondaryText: 'Programmer',
  tertiaryText: 'In a meeting',
  optionalText: 'Available at 4:00pm',
}

const itemsWithHeading: IBreadcrumbItem[] = [
  { text: 'Home', key: 'Files', onClick: undefined },
  { text: 'Custom Software Department', key: 'd1', onClick: undefined },
  { text: 'Marko Savic', key: 'd2', isCurrentItem: true, as: 'h4' },
]

export const App = () => {
  const [view, setView] = React.useState(1)
  const [isOpen, { toggle: showNav }] = useBoolean(true)

  // Change View
  const changeView = (view: number) => {
    switch (view) {
      case 1:
        return <OsnovnaSkolaJedan />
      case 2:
        return <OsnovnaSkolaDva />
      case 3:
        return <GimnazijaJedan />
      case 4:
        return <GimnazijaDva />
      case 5:
        return <SSJedan />
      case 6:
        return <SSDva />
      case 7:
        return <UmetnickaJedan />
      case 8:
        return <UmetnickaDva />
      default:
        return <div />
    }
  }

  // Navigation
  const navGroups: INavLinkGroup[] = [
    {
      links: [
        {
          key: '0',
          name: 'Основна школа 1.1',
          url: '',
          onClick: () => setView(1),
        },
        {
          key: '1',
          name: 'Основна школа 1.2',
          url: '',
          onClick: () => setView(2),
        },
        {
          key: '2',
          name: 'Гимназија 2.1',
          url: '',
          onClick: () => setView(3),
        },
        {
          key: '3',
          name: 'Гимназија 2.2',
          url: '',
          onClick: () => setView(4),
        },
        {
          key: '4',
          name: 'Стручна 3.1',
          url: '',
          onClick: () => setView(5),
        },
        {
          key: '5',
          name: 'Стручна 3.2',
          url: '',
          onClick: () => setView(6),
        },
        {
          key: '6',
          name: 'Уметничка 4.1',
          url: '',
          onClick: () => setView(7),
        },
        {
          key: '7',
          name: 'Уметничка 4.2',
          url: '',
          onClick: () => setView(8),
        },
      ],
    },
  ]

  return (
    <div className="App">
      <Stack className="app-header" horizontal={true} horizontalAlign="space-between">
        <div className="app-logo">
          <span>SAGA</span>
        </div>
        <div className="app-user">
          <Persona
            {...examplePersona}
            // tslint:disable-next-line: jsx-no-lambda
            onRenderSecondaryText={(props, defaultRender) =>
              defaultRender && props ? <div className="ms-hiddenSm">{defaultRender(props)}</div> : null
            }
            // tslint:disable-next-line: jsx-no-lambda
            onRenderPrimaryText={(props, defaultRender) =>
              defaultRender && props ? <div className="ms-hiddenSm">{defaultRender(props)}</div> : null
            }
            styles={{
              details: {
                order: -1,
              },
              primaryText: {
                ':hover': {
                  color: '#fff',
                },
                color: '#fff',
              },
              secondaryText: {
                textAlign: 'right',
                color: '#ccc',
              },
            }}
            size={PersonaSize.size40}
            presence={PersonaPresence.online}
            imageAlt="Marko Savic status is online."
          />
        </div>
      </Stack>
      <div className="app-container">
        <div
          style={{
            ...(isOpen ? { width: '17rem', transition: 'all 0.3s' } : { width: '2rem' }),
          }}
          className="app-sidemenu ms-hiddenSm"
        >
          <IconButton
            onClick={showNav}
            style={{ alignSelf: 'flex-end' }}
            iconProps={{
              iconName: isOpen ? 'DoubleChevronLeft8' : 'DoubleChevronRight8',
            }}
          />
          <div style={{ overflow: 'auto' }}>
            <Nav
              styles={{
                root: {
                  // Todo: Custom Scrollbar
                  overflow: 'hidden',
                },
              }}
              groups={navGroups}
            />
          </div>
        </div>
        <div className="app-content">{changeView(view)}</div>
      </div>
    </div>
  )
}
